<template>
    <div class="flex-body">
        <v-header></v-header>
        <v-mainTitle></v-mainTitle>
        <v-mainMenu :isShowMenu="isShowMenu"></v-mainMenu>
        <v-loading></v-loading>
        <v-loading></v-loading>
        <v-prompt></v-prompt>
        <v-prompts></v-prompts>
        <transition :name="transitionName"> 
            <keep-alive>
                <router-view></router-view>
            </keep-alive>
        </transition>
    </div>
</template>
<script>
    // require("src/assets/common.css")
    import {mapGetters} from 'vuex'
    import mainTitle from './components/mainTitle'
    import mainMenu from './components/mainMenu'
    import loading from './components/loading'
    import header from './components/header'
    import prompt from './components/prompt'
    import prompts from './components/prompts'
    export default {
        data(){
            return {
                transitionName:'slide-left'
            }
        },
        //(data observer) watch/event $el 
        created:function(){
            if (this.$route.name == undefined) {
                return this.$router.push('login')
            }  
            if (this.$route.name == 'login') {
                this.$store.commit('UPDATE_TITLE','login')
            }
        },
        //watch Vue  $watch() watch 
        watch:{
            '$route' (to,from){
                // alert(from.name)
                this.$store.commit('UPDATE_STATE' , { 
                    title : to.name,
                    common_menu : true,
                    isShowMenu:false,
                    loading : false 
                });
            }
        },
        computed: {
            ...mapGetters([
                'loading' ,
                'common_menu',
                'isShowMenu'
            ])
        },
        components:{
            'v-mainMenu' : mainMenu , 
            'v-mainTitle' : mainTitle,
            'v-header' : header,
            'v-loading' : loading,
            'v-prompt' : prompt, 
            'v-prompts' : prompts
        }
    }
</script>
<style>
    /* 121   */
    .slide-left-leave-active {transition: all .2s ; }
    /* 1 21 */
    .slide-left-leave-active{ transform: translateX(-700px); opacity: 0;  }
    /* 122   */
    .slide-left-enter-active {transition: all .3s linear .2s; }
    /* 2   */
    .slide-left-enter { transform: translateX(700px); opacity: 0; }
</style>


